.head-x {
    height: 100px;
    display: flex;
    justify-content: space-between;
    border: 2px solid black;
    background-color: skyblue;
    width: 100%;

    .currentIndex-1 {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-left: 0px;
        margin-bottom: 60px;
        font-size: 2rem;
        height: 100%;
        font-weight: bold;
        width: 80%;
    }

    .time {
        // background-color: red;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 40%;
    }

    .timeTag {
        margin-top: 30px;
        margin-bottom: 20px;
        display: flex;
        justify-content: space-evenly;
        text-align: center;
        align-items: center;
        height: 45px;
        width: 100px;
        background-color: yellowgreen;
        border-radius: 13px;
        border: 2px solid black;

        .clk {
            transform: scale(0.9);
        }
    }
}
.alert {
    width: 100%;
    margin-top: -50px;
    align-items: center;
    text-align: center;
    height: 30px;
}

.Question {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    // background-color: orange;
    // margin-top: -50px;
    .Question-1 {
        margin-top: 60px;
        height: 100px;
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;

        .lifeline {
            width: 10%;
            display: flex;
            justify-content: center;
            align-items: center;
        }

        .lifeline-icon {
            display: inline-block;
            /* Make sure the element can be transformed */
            transform: scale(1.5);
            color: green;
            margin-right: 5px;
        }

        .lifeline-icon:hover {
            display: inline-block;
            /* Make sure the element can be transformed */
            transform: scale(1.6);
            color: red;
        }

        h1 {
            text-align: center;
            width: 80%;
        }

        .hints {
            width: 10%;
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }

    // height: 80vh;
    height: 627px;
    width: 100vw;
    color: black;
    background-color: rgb(233, 253, 202);

    .currentIndex {
        color: black;
        // border: 2px solid black;
        width: 100%;
        margin-left: 50px;
        display: flex;
        justify-content: flex-start;
        font-size: 1.5rem;
        font-weight: bold;
    }

    .options {
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        background-color: rgba(188, 255, 172, 1);
        width: 100%;
        height: 300px;

        .col-1 {
            // width: 340px;
            width: 50%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            // background-color: rgb(253, 98, 98);

            .opt-1 {
                list-style: none;
                text-decoration: none;
                text-align: center;
                height: 40px;
                width: 80%;
                border-radius: 13px;
                margin-bottom: 20px;

                // background-color: black;
                button {
                    background-color: orchid;
                    height: 40px;
                    width: 60%;
                    border-radius: 13px;
                }

                button:hover {
                    transform: scale(1.05);
                }
            }

            .opt-2 {
                list-style: none;
                text-decoration: none;
                text-align: center;
                height: 40px;
                width: 80%;
                border-radius: 13px;

                // background-color: black;
                button {
                    background-color: orchid;
                    height: 40px;
                    width: 60%;
                    border-radius: 13px;
                }

                button:hover {
                    transform: scale(1.05);
                }
            }
        }

        .col-2 {
            // width: 340px;
            width: 50%;
            // background-color: rgb(246, 87, 87);
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            .opt-3 {
                list-style: none;
                text-decoration: none;
                height: 40px;
                width: 80%;
                border-radius: 13px;
                margin-bottom: 20px;
                // background-color: black;
                display: flex;
                justify-content: center;
                align-items: center;

                button {
                    background-color: orchid;
                    height: 40px;
                    width: 60%;
                    border-radius: 13px;
                }

                button:hover {
                    transform: scale(1.05);
                }
            }

            .opt-4 {
                display: flex;
                justify-content: center;
                align-items: center;
                list-style: none;
                text-decoration: none;
                text-align: center;
                height: 40px;
                width: 80%;
                border-radius: 13px;

                // background-color: black;
                button {
                    background-color: orchid;
                    height: 40px;
                    width: 60%;
                    border-radius: 13px;
                }

                button:hover {
                    transform: scale(1.05);
                }
            }
        }
    }

    footer {
        width: 100%;
        margin-top: 60px;
        display: flex;
        justify-content: space-between;

        button {
            height: 45px;
            width: 300px;
            background-color: greenyellow;
            border-radius: 13px;
            // margin-top: -50px;
        }

        .x {
            // margin-left: 20px;
            list-style: none;
            text-decoration: none;
            font-weight: bold;
            color: goldenrod;
        }

        .y {
            list-style: none;
            text-decoration: none;
            font-weight: bold;
            color: goldenrod;
        }

        .z {
            // list-style: none;
            // text-decoration: none;
            // font-weight: bold;
            // color: goldenrod;
            // margin-right: 20px;
        }

        .submit {
            list-style: none;
            text-decoration: none;
            font-weight: bold;
            color: goldenrod;
            height: 100%;
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
        }

        .back {
            height: 100%;
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            color: orange;
        }

        .next {
            height: 100%;
            width: 100%;
            display: flex;
            justify-content: center;
            color: orange;
            align-items: center;
        }
    }
}

.toast-valid {
    background-color: greenyellow;
    color: black;
    border-radius: 13px;
    text-align: center;
    height: 30px;
    width: 200px;
    transform: translate(30px, -275px);
}

.toast-invalid {
    background-color: red;
    border-radius: 13px;
    text-align: center;
    height: 30px;
    width: 200px;
    transform: translate(30px, -275px);
}

