                display: flex;
                justify-content: center;
                align-items: center;
            }
        }
    }

    .Question {
        // background-color: orange;

        .options {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            width: 100%;
            // background-color: white;

            .col-1 {
                width: 100%;

                .opt-1 {
                    width: 100%;
                }

                .opt-2 {
                    width: 100%;
                }
            }

            .col-2 {
                margin-top: 20px;
                width: 100%;

                .opt-3 {
                    width: 100%;
                }

                .opt-4 {
                    width: 100%;
                }
            }
        }
    }
}
